import axios from 'axios';
import { mapGetters } from 'vuex';
/*
import { mapActions } from 'vuex';
*/
export default {
  data() {
    return {
      username: '',
      email: '',
      phone: '',
      password: '',
      exams: [],
      errors: [],
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']),
  },
  methods: {
    RandomKey() {
      return (
        Math.random().toString(36).substring(2, 15) +
        Math.random().toString(36).substring(2, 15)
      );
    },
  },
  //Fetching Exams when component is mounted
  created() {
    axios
      .get(process.env.VUE_APP_Exams_Api + '/exams')
      .then((response) => {
        this.exams = response.data;
      })
      .catch((error) => {
        this.errors.push(error);
      });
  },
};
