import router from '../router';

export default {
  setLogOut(state, payload) {
    state.isLoggedIn = payload;
    router.push(process.env.VUE_APP_BaseURL);
  },
  setLogIn(state, payload) {
    state.isLoggedIn = payload;
  },
  Register(state, payload) {
    fetch(`${process.env.VUE_APP_Register_Api}`,{
         method: "POST",
         headers: {
             'Content-Type': 'application/json',
             'accept': 'application/json',
             'Authorization': `Bearer ${process.env.VUE_APP_Auth_Token}`
         },
         body:JSON.stringify({
             name: payload.username,
             username: payload.username,
             email: payload.email,
             password: payload.password,
             roles: [
               "author"
             ]
         })
     }).then(function(response){
         return response.json()
     }).then(function(post){
         console.log(post)
     });
 },
 Login(state, payload) {
          fetch(`${process.env.VUE_APP_Login_Api}`,{
         method: "POST",
         headers: {
             'Content-Type': 'application/json',
             'accept': 'application/json',
         },
         body:JSON.stringify({
             username: payload.username,
             password: payload.password,
         })
     }).then(function(response){
         return response.json()
     }).then(function(post){
        //store token
         state.LoginToken = post.token;
         if(state.LoginToken) {
          state.isLoggedIn = true;
          state.username = payload.username;
         }
     });
 },
 PostExam(state, payload) {
            fetch(`${process.env.VUE_APP_Post_Answer_Api}`,{
             method: "POST",
             headers: {
                 'Content-Type': 'application/json',
                 'accept': 'application/json',
                    'Authorization': `Bearer ${state.LoginToken}`
                },
                body:JSON.stringify({
                    title: payload.title,
                    status: payload.status,
                    acf: {
                        UserName: payload.UserName,
                        ExamQuestion: payload.Question,
                        ExamAnswer: payload.Answer,
                        AnswerWeight: payload.AnswerWeight,
                        ExamId: payload.ExamId,
                    }
                })
            }).then(function(response){
                return response.json()
            }
            ).then(function(){
                //change page to exam result
                router.push({name: 'ExamResult', params: {QuizId: payload.ExamId}, query: {QuizTitle: payload.ExamName}});
            }
            );
        }

};
