<template>
  <section
    class="
      min-h-screen
      flex flex-col
      font-IranSans
      bg-slate-50
      snap-y snap-proximity
    "
  >
    <!-- --------------- Main content --------------- -->
    <main class="min-h-full">
      <div
        class="h-56 w-full bg-[#E9ECEE] flex items-center justify-start"
      ></div>
      <section class="lg:container px-4 mb-6 relative -top-28">
        <div class="bg-white rounded-xl shadow-lg p-3">
          <!-- ----- stepes ------ -->
          <div class="px-6 py-3 flex justify-center items-center">
            <!-- start -->
            <div
              class="hidden lg:flex flex-col gap-3 items-center justify-center"
            >
              <div
                class="
                  p-5
                  flex
                  items-center
                  justify-center
                  bg-[#946E5F]
                  rounded-full
                "
              >
                <img src="/assets/icon/Location.svg" />
              </div>
              <span class="text-[#946E5F] text-xs font-bold">شروع آزمون</span>
            </div>
            <!-- divider -->
            <div class="hidden lg:block w-20 h-px relative -top-4 bg-slate-200">
              <div
                class="w-20 h-px absolute top-0 right-0 bg-primary-600"
              ></div>
            </div>
            <!-- step one -->
            <div
              v-for="(step, index) in StepCount"
              :key="index"
              class="flex flex-col gap-3 items-center justify-center"
            >
              <div
                class="
                  p-5
                  flex
                  items-center
                  justify-center
                  bg-primary-600
                  rounded-full
                "
              >
                <div class="w-5 h-5 rounded-full bg-white"></div>
              </div>
              <span class="text-primary-600 text-xs font-bold px-5"
                >مرحله {{ step }}</span
              >
            </div>
            <!-- divider -->
            <div class="hidden lg:block w-20 h-px relative -top-4 bg-slate-200">
              <div
                class="w-1/2 h-px absolute top-0 right-0 bg-primary-600"
              ></div>
            </div>
            <!-- step four -->
            <div
              class="hidden lg:flex flex-col gap-3 items-center justify-center"
            >
              <div
                class="
                  p-5
                  flex
                  items-center
                  justify-center
                  bg-slate-200
                  rounded-full
                "
              >
                <img src="/assets/icon/Result-doc-light.svg" />
              </div>
              <span class="text-slate-500 text-xs font-bold">نتیجه آزمون</span>
            </div>
          </div>
          <!-- divider -->
          <div class="h-px w-full bg-slate-200 my-3"></div>
          <!-- ----- content ------ -->
          <form method="post" @submit.prevent="SubAns">
            <div class="flex flex-col gap-10 px-6 py-5">
              <div
                v-for="(exam, index) in FilteredExams[0].Questions"
                v-show="index < this.stageNumber"
                :class="{
                  dspnone:
                    ActiveStages &&
                    index < this.stageNumber - this.StageOnChange,
                }"
                :key="index + RandomKey()"
                class="flex flex-col gap-5"
              >
                <!-- questions -->
                <p class="text-secondary-600 font-bold">
                  {{ index + 1 }} - {{ exam.question }}
                </p>
                <!-- ----- answers ------ -->
                <div
                  v-for="(innerExam, innerIndex) in FilteredExams[0].Questions[
                    index
                  ].Answers"
                  :key="innerIndex + RandomKey()"
                  class="flex gap-3 items-center"
                >
                  <input
                    type="radio"
                    :name="
                      innerIndex !== 0
                        ? 'answer' + this.RadioNumber
                        : ++this.RadioNumber && 'answer' + this.RadioNumber
                    "
                    :value="{
                      Answer: innerExam.answer,
                      AnswerWeight: innerExam.AnswerWeight,
                      Question: exam.question,
                    }"
                    v-model="ExamAnswers[index]"
                    :id="++RadioNumber"
                  />
                  <label class="text-sm text-secondary-600" :for="RadioNumber">
                    {{ innerExam.answer }}
                  </label>
                </div>
              </div>
              <!--display error section -->
              <div
                v-for="(empty, index) in emptyanswers"
                :key="index + RandomKey()"
                class="error"
              >
                پاسخ سوال {{ empty }} وارد نشده است
              </div>
              <div class="flex items-center gap-5">
                <button
                  type="button"
                  @click="PreStage"
                  class="
                    px-6
                    py-3
                    rounded
                    flex
                    gap-3
                    items-center
                    bg-secondary-100
                  "
                >
                  <img src="/assets/icon/Arrow-Right-gray.svg" />
                  <span class="text-sm text-secondary-500">بازگشت</span>
                </button>
                <input
                  type="button"
                  @click="NextStage"
                  value="مرحله بعدی"
                  class="
                    text-white
                    px-6
                    py-3
                    rounded
                    flex
                    gap-3
                    items-center
                    bg-gradient-to-r
                    from-primary-500
                    to-primary-800
                  "
                />
                <span class="text-sm text-white">مرحله بعدی</span>
                <img src="/assets/icon/Stroke-Left-light.svg" />
              </div>

              <input
                type="button"
                v-if="StepCount == 0"
                @click="SubAns"
                value="مشاهده نتیجه"
                class="
                  text-white
                  px-6
                  py-3
                  rounded
                  flex
                  gap-3
                  items-center
                  bg-gradient-to-r
                  from-primary-500
                  to-primary-800
                "
                style="width: 140px"
              />
              <span class="text-sm text-white">مشاهده نتیجه</span>
            </div>
          </form>
        </div>
      </section>
    </main>
  </section>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
import axios from 'axios';

export default {
  props: ['QuizTitle'],
  data() {
    return {
      ActiveStages: false,
      stageNumber: null,
      StageOnChange: null,
      StageCount: null,
      RadioNumber: 1,
      StepCount: [],
      exams: [],
      FilteredExams: [],
      errors: [],
      emptyanswers: [],
      ExamAnswers: [],
      ExamId: null,
      PostToServer: false,
    };
  },
  //Fetching Exams when component is mounted
  async created() {
    await axios
      .get(process.env.VUE_APP_Exams_Api + '/exams')
      .then((response) => {
        this.exams = response.data;
        /*filter exams by quiz title*/
        this.FilteredExams = this.exams.filter(
          (exam) => exam.ExamTitle === this.QuizTitle
        );
      })
      .catch((error) => {
        this.errors.push(error);
      });
    /*set stage number*/
    const QuestionsCount = this.FilteredExams[0].Questions.length;
    this.stageNumber = 2;
    /* set a constant number to add to stage number */
    this.StageOnChange = 2;
    this.StageCount = QuestionsCount / this.stageNumber;
    /*set steps cout*/
    for (let i = 0; i < this.StageCount - 1; i++) {
      this.StepCount.push(i + 1);
    }
  },
  computed: {
    ...mapGetters(['LoginToken', 'isLoggedIn', 'username']),
  },
  methods: {
    ...mapActions(['postExam']),
    RandomKey() {
      return (
        Math.random().toString(36).substring(2, 15) +
        Math.random().toString(36).substring(2, 15)
      );
    },
    SubAns() {
      if(this.emptyanswers.length == 0){
         this.PostToServer = true;
      }
      //generate exam id
      this.ExamId = this.RandomKey();
      //check if answers are empty
      if (this.ExamAnswers.length < this.FilteredExams[0].Questions.length) {
        alert('لطفا همه جواب ها را وارد کنید');
        this.PostToServer = false;
      }
      if (this.ExamAnswers.length == this.FilteredExams[0].Questions.length) {
        //clear emty answers array
        this.emptyanswers = [];
        //fill error fields
        for (let i = 0; i < this.ExamAnswers.length; i++) {
          //check if one member of answer array is null
          if (this.ExamAnswers[i] == null) {
            this.emptyanswers.push(i + 1);
          }
          //post to server
          if (this.PostToServer == true) {
            //post answers to server
            this.postExam({
              title: `${this.ExamId}-${this.QuizTitle}`,
              status: 'publish',
              Answer: this.ExamAnswers[i].Answer,
              AnswerWeight: this.ExamAnswers[i].AnswerWeight,
              Question: this.ExamAnswers[i].Question,
              UserName: this.username,
              ExamId: this.ExamId,
              ExamName: this.QuizTitle,
            });
          }
        }
      }
    },
    NextStage() {
      /*set stage number*/
      this.stageNumber += this.StageOnChange;
      /* minus one from first of array */
      this.StepCount.shift();
      /* active stages */
      this.ActiveStages = true;
    },
    PreStage() {
      /*set stage number*/
      if (this.stageNumber > this.StageOnChange) {
        this.stageNumber -= this.StageOnChange;
      }
    },
  },
};
</script>

<style scoped>
.dspnone {
  display: none;
}
.error {
  color: red;
}
</style>