<script>
import axios from 'axios';
export default {
  data() {
    return {
      ContactData: '',
      errors: [],
    };
  },
  //Fetching Exams when component is mounted
  created() {
    axios
      .get(process.env.VUE_APP_Exams_Api + '/contactus')
      .then((response) => {
        this.ContactData = response.data;
      })
      .catch((error) => {
        this.errors.push(error);
      });
  },
};
