<script>
import axios from 'axios';
export default {
  props: ['QuizId'],
  data() {
    return {
      Answers: [],
      FilteredAnswers: [],
      exams: [],
      FilteredExams: [],
      QuizTitle: '',
      AnsScore: 0,
    };
  },
  computed: {
    //calculate the total number of answers
    CalcAnsCount() {
      return this.FilteredAnswers.length;
    },
    //dsp quiz title
    DspQuizTitle() {
      return this.$route.query.QuizTitle;
    },
  },
  methods: {},
  async created() {
    await axios
      .get(process.env.VUE_APP_Post_Answer_Api, {
        params: {
          per_page: 100,
        },
      })
      .then((response) => {
        this.Answers = response.data;
        /*filter exams by quiz title*/
        this.FilteredAnswers = this.Answers.filter(
          (Answers) => Answers.acf.ExamId == this.QuizId
        );
        //calculate the total weight of the answers
        let score = 0;
        this.FilteredAnswers.forEach((ans) => {
          score += parseInt(ans.acf.AnswerWeight);
          this.AnsScore = score;
        });
      })
      .catch((error) => {
        this.errors.push(error);
      });

    //get request to get exam options
    await axios
      .get(process.env.VUE_APP_Exams_Api + '/exams')
      .then((response) => {
        this.exams = response.data;
        /*filter exams by quiz title*/
        this.FilteredExams = this.exams.filter(
          (exam) => exam.ExamTitle === this.$route.query.QuizTitle
        );
      })
      .catch((error) => {
        this.errors.push(error);
      });
  },
};
</script>

