import { createRouter, createWebHistory } from 'vue-router';
import MainPage from './pages/MainPage.vue';
import ExamStart from './pages/ExamStart.vue';
import ExamSteps from './pages/ExamSteps.vue';
import AboutPage from './pages/AboutPage.vue'
import ContactUS from './pages/ContactUs.vue'
import FaqPage from './pages/FaqPage.vue'
import ConsultantPage from './pages/ConsultantPage.vue'
import ExamResult from './pages/ExamResult.vue'
import store from './store/index.js';

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: process.env.VUE_APP_BaseURL, redirect: '/queez'},
    { name: 'MainPage', path: '/queez', component: MainPage },
    { name: 'startqueez', path: '/startqueez/:QuizTitle', component: ExamStart, props: true, meta: {requiresAuth: true}},
    { name: 'examsteps',  path: '/examsteps/:QuizTitle', component: ExamSteps, props: true, meta: {requiresAuth: true}},
    { name: 'ConsultantPage', path: '/consultantpage', component: ConsultantPage, meta: {requiresAuth: true}},
    { name: 'ExamResult', path: '/examresult/:QuizId', component: ExamResult, meta: {requiresAuth: true}, props: true},
    { name: 'AboutPage',  path: '/aboutpage', component: AboutPage },
    { name: 'ContactUs',  path: '/contactus', component: ContactUS }, 
    { name: 'FaqPage', path: '/faqpage', component: FaqPage },
    { path: "/:catchAll(.*)", redirect: '/queez'}
  ],
});


router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    if (!store.getters.isLoggedIn) {
      next({ name: 'MainPage' })
    } else {
      next() // go to wherever I'm going
    }
  } else {
    next() // does not require auth, make sure to always call next()!
  }
})


export default router;
