<script>
import axios from 'axios';
export default {
  data() {
    return {
      AboutData: '',
      errors: [],
    };
  },
  //Fetching Exams when component is mounted
  created() {
    axios
      .get(process.env.VUE_APP_Exams_Api + '/aboutus')
      .then((response) => {
        this.AboutData = response.data;
      })
      .catch((error) => {
        this.errors.push(error);
      });
  },
};
