<script>
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      Dsp: false,
    };
  },
  methods: {
    ...mapActions(['register', 'login']),
    DspReg() {
      this.Dsp = false;
    },
    DspLog() {
      this.Dsp = true;
    },
    Register() {
      this.register({
        username: this.username,
        email: this.email,
        phone: this.phone,
        password: this.password,
      });
    },
    Login() {
      this.login({
        username: this.username,
        password: this.password,
      });
    },
  },
  computed: {
    ...mapGetters(['isLoggedIn']),
  },
};
</script>

